<template>
    <el-container style="height: 100%; width: 100%; border: 1px">
        <el-header>
            <el-menu class="el-menu-demo" mode="horizontal">
                <el-menu-item index="1" @click="toRecent">最近使用</el-menu-item>
                <el-menu-item index="2" @click="toMydoc">我的创建</el-menu-item>
                <el-menu-item index="3" @click="toFavour">我的收藏</el-menu-item>
            </el-menu>
            <div>我的收藏</div>
        </el-header>

        <el-main>
            <!--文档卡片组-->
            <el-row>
                <el-col v-for="(item, index) in Files" :key="index" :span="4" @click.native="detail(item)">
                    <div v-if="item.isdelete == 0">
                        <el-card shadow="hover" style="margin-left: 20px">
                            <div class="card-menu" style="text-align:right">
                                <el-dropdown placement="bottom">
                                    <div class="card-pic">
                                        <span class="el-dropdown-link">
                                             <i class="el-icon-setting el-icon--right"/>
                                        </span>
                                    </div>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item @click.native="detail(item)">
                                            <i class="el-icon-folder-opened"/>打开
                                        </el-dropdown-item>
                                        <el-dropdown-item @click.native="deleteFavourite(item)">
                                            <i class="el-icon-collection-tag"/>移除收藏
                                        </el-dropdown-item>
                                        <el-dropdown-item @click.native="setLink(item) ; linkvisible=true">
                                            <i class="el-icon-chat-line-round"/>分享
                                        </el-dropdown-item>
                                        <el-dropdown-item @click.native="open(item)">
                                            <i class="el-icon-setting"/>加入团队文档
                                        </el-dropdown-item>
                                        <el-dropdown-item style="color:red" @click.native="deleteContent(item)">
                                            <i class="el-icon-delete"/>删除
                                        </el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </div>
                            <div class="card-body">
                                <!-- 文档图标 -->
                                <div class="card-picture">
                                    <span style="font-size:40px">
                                     <i class="el-icon-document"></i>
                                    </span>
                                </div>
                                <!-- 文档标题、创建时间 -->
                                <div class="card-word">
                                    <div style="font-size:20px">{{item.title}}</div>
                                    <div style="font-size:10px">创建：{{item.createtime}}</div>
                                    <div style="font-size:10px">修改：{{item.changetime}}</div>
                                </div>
                            </div>
                        </el-card>
                        <template> <!--分享框-->
                            <a-modal
                                    v-model="linkvisible"
                                    title="分享文件"
                                    ok-text="确认"
                                    cancel-text="取消"
                                    @ok="linkvisible=false"
                                    style="z-index:10010"
                            >
                                <a-input-search
                                        size="large"
                                        :placeholder="documentlinks"
                                        disabled
                                >
                                    <a-button slot="enterButton" v-clipboard:copy="documentlinks">
                                        <!--粘贴失败或者成功的提示，应该用不到 v-clipboard:success="onCopy"
                                        v-clipboard:error="onError"-->
                                        复制链接
                                    </a-button>
                                </a-input-search>
                            </a-modal>
                        </template>
                    </div>
                </el-col>
            </el-row>

            <el-pagination background layout="prev, pager, next" :total="pageCount"></el-pagination>
        </el-main>
    </el-container>
</template>

<script>
    import {deleteFavourite, getContent, getUserInfo, partial_updateContent} from "../../api/api";

    export default {
        inject: ['reload'],
        name: "Favour",
        components: {},
        data() {
            return {
                isLogin: false,
                visible: false,
                linkvisible:false,
                userinfo: {
                    id: "",
                    username: "",
                    email: "",
                },
                page: {
                    page: 1,
                    page_size: 10,
                },
                pageCount: 1,
                Files: [],
                documentlinks: ""
            };
        },
        created() {
            //此为创建页面时自动进行的
            let users = JSON.parse(localStorage.getItem("user"));
            console.log(users.id > 0);
            if (users.id > 0) {
                this.isLogin = true;
                this.getUserInfo();
                this.getFileList();
            } else {
                this.isLogin = false;
            }
            //this.getFileList();
        },
        watch: {},
        methods: {
            toIndex() {
                this.$router.push({path: "/"});
            },
            toEditor() {
                this.$router.push({path: "/editor"});
            },
            toPmsg() {
                let users = JSON.parse(localStorage.getItem("user"));
                let {id} = users;
                console.log(id);
                 this.$router.push({path: "/pmsg", query: {id}});
            },
            toRecent() {
                this.$router.push({path: "/recent"});
            },
            toFavour() {
                this.$router.push({path: "/favour"});
            },
            toMydoc() {
                this.$router.push({path: "/mydoc"});
            },
            toTrash() {
                this.$router.push({path: '/trash'});
            },
            /* 控制权限modal */
            showModal() {
                this.visible = true;
            },
            hideModal() {
                this.visible = false;
            },
            getUserInfo() {
                //从后端获取用户信息
                let users = JSON.parse(localStorage.getItem("user"));
                getUserInfo({id: users.id}).then((res) => {
                    let {
                        data: {id, username, email},
                    } = res;
                    this.userinfo = {
                        id: id,
                        username: username,
                        email: email,
                    };
                    console.log(this.userinfo);
                });
            },
            getFileList() {
                let users = JSON.parse(localStorage.getItem("user"));
                getUserInfo({id: users.id}).then((res) => {
                    this.Files = [];
                    for (let item of res.data.user_favs) {
                        console.log(item);
                        getContent({id: item.cid}).then((res) => {

                            let {data} = res;
                            console.log(data);
                            let obj = {
                                id: data.id,
                                title: data.title,
                                createtime: data.createtime,
                                changetime: data.changetime,
                                isdelete: data.isdelete,
                                fid: item.id,
                            };
                            console.log(obj);
                            this.Files.push(obj);
                        });
                    }
                });
            },
            handleRightMenuClick() {
            },
            open(item) {
                this.$prompt('请输入要将该文档加入的团队id', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputErrorMessage: '输入不能为空',
                    inputValidator: (value) => {       // 点击按钮时，对文本框里面的值进行验证
                        if (!value) {
                            return '输入不能为空';
                        }
                    },
                }).then(({value}) => {
                    partial_updateContent({
                            id: item.id,
                            tid: value,
                        }
                    ).then((res) => {
                        console.log(res);
                        if (res.data.id != null) {
                            this.$message({
                                message: "添加成功",
                                type: "success",
                            });
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'
                    });
                });
            },
            //跳转详情
            detail(item) {
                let {id} = item;
                console.log(id);
                this.$router.push({
                  path: "/doc", query: {
                    key: this.$Base64.encode(JSON.stringify({id}))
                  }
                });
            },
            deleteFavourite(item) {
                console.log(item);
                deleteFavourite({
                        id: item.fid,
                    }
                ).then((res) => {
                    console.log(res);

                    if (res.data.flag === "success") {
                        this.$message({
                            message: "移除成功",
                            type: "success",
                        });
                    }
                });
            },
            deleteContent(item) {
                partial_updateContent({
                        id: item.id,
                        isdelete: 1,
                    }
                ).then((res) => {
                    console.log(res);
                    if (res.data.id != null) {
                        this.$message({
                            message: "删除成功",
                            type: "success",
                        });
                    }
                });
            },
            setLink(item) {
                let {id} = item;
                console.log(id);
                this.documentlinks= "http://localhost:8080/#/doc" + "?key=" + this.$Base64.encode(JSON.stringify({id}));
            }
        },
    };
</script>

<style scoped>
</style>